p {
  font-size: larger;
}
.live {
  text-align: center;
  margin-top: 100px 0px 0px 0px;
  animation: live 3s ease-out infinite normal;
  font-size: 2em;
  opacity: 0.8;
}
.headp {
  background-color: rgb(116, 109, 109);
  padding: 0px 40px 0px 30px;
  width: fit-content;
  margin-left: 331px;
}
@keyframes live {
  0% {
    transform: scale(0.9);
  }
  25% {
    transform: scale(1.2);
  }
  60% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(0.9);
  }
}

.head {
  padding: 0;
  margin: 0;
}

.card {
  flex: auto;
}

.card_inner {
  font-family: "Segoe UI", Tahoma, Verdana, sans-serif;
  color: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
  border-radius: 7%;
  padding: 30px;
  width: 130px;
  margin: 20px;
  margin-left: 80px;
  height: 111px;
  background-color: #fa631d;
  transition: transform 0.4s; /* Animation */
}
body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "poppins", sans-serif;
  min-height: 100vh;
  background-color: #ffffff;
  position: relative;
  background-image: url("https://wearecovert.com/wp-content/uploads/2020/03/C19Virus_1920_1080_01.jpg");
  background-repeat: no-repeat;
 
  opacity: 0.8;
}
section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-radius: 10%;
  align-items: center;
  margin-right: 70px;
}

.card_inner:hover {
  transform: scale(1.1); /* (150% zoom)*/
}

